/**
 * Name: Bizagi Workportal Desktop Paginator Controller
 * Author: Mauricio Snchez
 */
bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.paginator", {}, {
  /**
                                                                                      *   Constructor
                                                                                      */
  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);
    //Load templates
    self.loadTemplates({
      "paginator": bizagi.getTemplate("bizagi.workportal.desktop.widget.paginator").concat("#paginator-wrapper"),
      "content": bizagi.getTemplate("bizagi.workportal.desktop.widget.paginator").concat("#paginator-content") });

  },

  /**
      * Renders the template defined in the widget
      */
  renderContent: function renderContent() {
    var self = this;
    var hide = self.params.referenceType === "ENTITY";
    var template = self.getTemplate("paginator" + self.getSuffixWidget());
    self.content = template.render({ hide: hide });

    return self.content;
  },

  /**
      * links events with handlers
      */
  postRender: function postRender() {
    var self = this;

    self.sub("PAGINATOR-UPDATE", $.proxy(self.updateView, self));
    self.configureHandlers();
  },

  /**
      *
      * @returns {string}
      */
  getSuffixWidget: function getSuffixWidget() {
    return "";
  },


  /**
      * Updates la view with the data related to entity:
      * Name entity
      * Number of records
      * Number of pages
      * */
  updateView: function updateView(ev, params) {
    var self = this;
    var args = params.args;
    var hide = args.referenceType === "ENTITY";
    var $content = self.getContent();

    self.setModel(args);
    $content.empty();

    if (args.fromActionLauncher) {
      if (self.totalRecords > 1) {
        renderTemplate();
      } else
      {
        self.clean();
      }
    } else
    {
      renderTemplate();
    }

    /**
       *
       */
    function renderTemplate() {
      var contentTemplate = self.getTemplate("content" + self.getSuffixWidget());

      contentTemplate.render({
        totalRecords: self.totalRecords,
        totalPages: self.totalPages,
        displayName: self.displayName,
        currentPage: self.currentPage,
        hide: hide }).
      appendTo($content);
    }
  },

  /**
      * Builds the navigation model
      */
  setModel: function setModel(args) {
    var self = this;

    self.totalRecords = typeof args.totalRecords == "undefined" ? self.totalRecords : args.totalRecords;
    self.totalPages = args.totalPages || self.totalPages;
    self.displayName = args.histName || self.displayName;
    self.currentPage = args.currentPage || self.currentPage;
    self.action = args.action;
    self.reference = args.reference || self.reference;
    self.surrogateKey = args.surrogateKey || self.surrogateKey;
    self.referenceType = args.referenceType;
    self.event = args.event;
    self.guidSearch = args.guidSearch;
  },

  /**
      *  Gets the current model
      */
  getModel: function getModel() {
    var self = this;

    return {
      page: self.currentPage,
      totalRecords: self.totalRecords,
      action: self.action,
      reference: self.reference,
      surrogateKey: self.surrogateKey,
      referenceType: self.referenceType,
      stopNavigation: true,
      guidSearch: self.guidSearch };

  },

  /**
      *  Binds events to handles
      */
  configureHandlers: function configureHandlers() {
    var self = this,
    $content = self.getContent();

    $content.on("click", $.proxy(self.onPageBreak, self));
  },

  /**
      * This event get a new page
      */
  onPageBreak: function onPageBreak(ev) {
    var self = this,
    $target = $(ev.target),
    $content = self.getContent();

    if ($target.hasClass("control-icon") && !$target.hasClass("ui-state-disabled")) {
      var type = $target.data("type");

      if (type == "forward") {
        self.currentPage += 1;
      } else
      if (type == "rewind") {
        self.currentPage -= 1;
      } else
      if (type == "fastrewind") {
        self.currentPage = 1;
      } else
      {
        self.currentPage = self.totalPages;
      }

      self.disabledPaginatorButtonsByLimits();
      $content.find(".paginator-controls__currentPage").text(self.currentPage);

      self.pub("notify", { type: "UPDATE-DATATEMPLATE-VIEW", args: self.getModel() });
    }
  },

  disabledPaginatorButtonsByLimits: function disabledPaginatorButtonsByLimits() {
    var self = this;
    var $buttonFastRewind = $("[data-type='fastrewind']", self.content);
    var $buttonRewind = $("[data-type='rewind']", self.content);
    var $buttonFordward = $("[data-type='forward']", self.content);
    var $buttonFastFordward = $("[data-type='fastforward']", self.content);

    if (self.totalPages === 1) {
      $buttonFastRewind.addClass("ui-state-disabled");
      $buttonRewind.addClass("ui-state-disabled");
      $buttonFordward.addClass("ui-state-disabled");
      $buttonFastFordward.addClass("ui-state-disabled");
    } else {
      // The first page
      if (self.currentPage === 1) {
        $buttonFastRewind.addClass("ui-state-disabled");
        $buttonRewind.addClass("ui-state-disabled");
        $buttonFordward.removeClass("ui-state-disabled");
        $buttonFastFordward.removeClass("ui-state-disabled");
      }
      // the last page
      else if (self.currentPage === self.totalPages) {
          $buttonFastRewind.removeClass("ui-state-disabled");
          $buttonRewind.removeClass("ui-state-disabled");
          $buttonFordward.addClass("ui-state-disabled");
          $buttonFastFordward.addClass("ui-state-disabled");
        }
        // other page
        else {
            $buttonFastRewind.removeClass("ui-state-disabled");
            $buttonRewind.removeClass("ui-state-disabled");
            $buttonFordward.removeClass("ui-state-disabled");
            $buttonFastFordward.removeClass("ui-state-disabled");
          }
    }
  },
  /**
      * Returns a new page close to the first item above based on the previous page
      * @param {*} newItemsPerPage new value in results per page in search
      * @param {*} previousItemsPerPage previous value in results per page
      * @param {*} previousPage previous value in page number
      */
  calculateNewPage: function calculateNewPage(newItemsPerPage, previousItemsPerPage, previousPage) {
    previousItemsPerPage = previousItemsPerPage || 10;
    var currentFirstItem = previousItemsPerPage * previousPage - previousItemsPerPage + 1;
    var newPage = Math.ceil(currentFirstItem / newItemsPerPage);
    return newPage ? newPage : 1;
  },

  /**
      *
      */
  clean: function clean() {
    var self = this;
    var $content = self.getContent();

    if ($content) {
      $content.empty();
    }
  } });


bizagi.injector.register("bizagi.workportal.widgets.paginator", ["workportalFacade", "dataService", bizagi.workportal.widgets.paginator]);